@import "import/reset";

body {
  font-family: arial;
  font-size: 14px ;
  font-weight: normal ;
  line-height:16px ;
  min-width: 320px;

  color:#fff ;
  background-color:#f6f3ef ;
  -webkit-font-smoothing: antialiased;
}
