p,
pre {
  margin:0 ;
}
a {

  text-decoration: none;
  color:#fcd45b ;
  &:active,
  &:focus,
  &:hover {

  }
  &:active,
  &:focus {

    outline: none;
  }
}
hr {
  margin:  0;
  border-top: none;
  border-right: none;

  border-left: none;
}
img,
picture {
  max-width: 100%;
  margin: 0;
}
.wrapper {
  position: relative;
  max-width: 2880px;
  min-width: 2310px;
  height: auto !important;
  height: 100%;
  min-height: 100%;
  margin: 0 auto;

  @extend .clearfix;
}
.container {
  position: relative;

}

header {
    background-color: #b32121;
    padding-top: 50px;
    padding-left: 60px;
    padding-right: 34px;
  .header--logo {
    display: block;
    float: left;
    &:hover{
      opacity: 0.8;
    }

    img {
      max-width: 100%;
    }
  }
}
nav {
  ul {
    @extend .clearfix;
    li {
      display: inline-block;
    }
  }
}

.headnav{
    float: left;
    margin: 40px;
    margin-left: 76px;
  li{
    float: left;
    padding-left: 50px;
    padding-right: 50px;
    position: relative;
    a{
      color: #f7e9d9;
      font-size: 30px;
      text-transform: uppercase;
      font-weight: bold;
      &:hover{
        opacity: 0.8;
      }

    }
    &.item-has-child{
      &:after{
    content: "";
    border: 9px solid transparent;
    border-top: 9px solid #f7e9d9;
    display: block;
    position: absolute;
    right: 14px;
    top: 1px;
      }
    }
    .sub-menu{
    display: none;
    position: absolute;
    top: 18px;
    z-index: 9;
    padding-top: 10px;
    li{
      text-align: left;
      padding: 0;
      width: 100%;
      a{
      text-align: left;
      font-size: 22px;
      line-height: 30px;
    }
    }

    }
    &:hover{
      .sub-menu{
      display: block;

    }
    }
  }
}


.header-rightside{
  float: right;

}


input.search{
background-color: rgba(0,0,0,.2);
    border: none;
    border-radius: 62px;
    color: #fff;
    text-transform: uppercase;
    padding: 36px;
    width: 550px;
    padding-left: 94px;
    font-size: 22px;
&:focus{
  outline:none;
}
}
::-webkit-input-placeholder {color:#fff;font-size: 22px;}
::-moz-placeholder          {color:#fff;font-size: 22px;}/* Firefox 19+ */
:-moz-placeholder           {color:#fff;font-size: 22px;}/* Firefox 18- */
:-ms-input-placeholder      {color:#fff;font-size: 22px;}

.search {
    position: relative;
    float: left;
    margin-right: 14px;

  button{
    width: 40px;
    height: 40px;
    background: none;
    border: none;
    background-image: url(../img/loop.png);
    position: absolute;
    left: 22px;
    top: 30px;
  }
}


.account,.cart{
  width: 100px;
  height: 100px;
  background: #f7e9d9;
  border-radius:50%;
  display: block;
  background-position: center center;
  background-repeat: no-repeat;
  float: left;
  margin-left: 18px;
  margin-right: 18px;

  &:hover{
    background-color: #fcd45b;
  }
}
.account{
  background-image: url(../img/men.png);

}
.cart{
  background-image: url(../img/cart.png);

}




.container .bx-wrapper{
      background-color: #b32121;
      border:none;
      box-shadow:none;
      background-image: url(../img/bg_sl.png);
      background-position: center bottom;
      background-repeat: no-repeat;
      margin: 0;

      .bx-viewport{
    padding-top: 20px;
    padding-bottom: 100px;
      }
      .bx-controls-direction{
        a{
    width: 121px;
    height: 469px;
    background-image: url(../img/arr_l_bg.png);
    background-position: 50%;
    margin-top: -316px;
    left: -2px;

          &:before{
            content: '';
            display: block;
            width: 28px;
            height: 48px;
            background-image: url(../img/arr_l.png);
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto;
          }
          &:hover{
            &:before{
              opacity: 0.8;
            }
          }
        }
        .bx-next{
          background-image: url(../img/arr_r_bg.png);
          left: auto;
          right: -2px;
          &:before{background-image: url(../img/arr_r.png);
          }
        }
      }
}

.slider-left_txt{
  float: left;
  padding-left: 360px;
  width: 50%;

  p{
font-size: 67px;
    color: #fbc31c;
    font-family: Satisfy,cursive;
    line-height: 80px;
    letter-spacing: 9px;
    padding-top: 296px;
  }
  h3{
    font-family: Luckiest Guy,cursive;
    font-size: 164px;
    color: #f7e9d9;
    line-height: 154px;
    text-transform: uppercase;
    text-shadow: -5px 5px 2px #d47b6d;
    margin-top: 60px;
    span{
      color: #fcd45b;
    }
  }
  .check{
width: 369px;
    margin-top: 156px;
    height: 162px;
    display: block;
    background-image: url(../img/check.png);
    color: #c92526;
    font-size: 38px;
    text-align: center;
    padding: 73px;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
    &:hover{
      opacity: 0.8;
    }

  }
}

.slider-right_img{
float: right;
    padding-right: 287px;
    width: 50%;
  .img_w{
    min-height:1446px;
    background-position: center center;
        background-size: contain;
        background-repeat: no-repeat;
  }
}



section {
  padding: 0 0 ;
}
article {
  @extend .clearfix;
}
.looper {
  @extend .clearfix;
}



